* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  background-color: #ecf0f1;
  font-family: Segoe UI, Helvetica Neue, sans-serif;
  font-size: 16px;
  line-height: 150%;
  color: #333;
  font-weight: 100; }

.container {
  margin: 0 auto;
  display: table;
  width: 100%;
  max-width: 980px;
  padding-left: 15px;
  padding-right: 15px; }

.hero {
  background-color: #6441A4;
  padding-top: 60px;
  padding-bottom: 60px;
  color: #f0f0f0;
  margin-bottom: 15px; }
  @media (max-width: 640px) {
    .hero {
      padding-top: 30px;
      padding-bottom: 30px; } }

.hero h1 {
  font-size: 64px;
  margin-bottom: 22.5px;
  font-weight: 100;
  line-height: 100%; }
  @media (max-width: 640px) {
    .hero h1 {
      font-size: 48px;
      margin-bottom: 15px; } }

.hero h4 {
  font-size: 24px;
  margin-bottom: 15px;
  font-weight: 100;
  color: rgba(240, 240, 240, 0.8); }
  @media (max-width: 640px) {
    .hero h4 {
      font-size: 24px; } }

.hero p {
  margin-bottom: 3.75px; }

.hero a {
  color: #6441A4;
  padding: 0.9375px 5px;
  background-color: #f0f0f0;
  text-decoration: none;
  border-radius: 2px;
  transition: background-color 0.2s, color 0.2s; }
  .hero a:hover {
    background-color: #432b6d;
    color: #f0f0f0; }

form {
  width: 100%; }

input, select {
  padding: 15px;
  border: 1px solid #ddd;
  display: inline-block;
  margin: 7.5px;
  width: calc( (100%/3) - (7.9365079365px));
  font-size: 16px;
  color: #333; }
  @media (max-width: 640px) {
    input, select {
      width: 100%;
      margin-left: 0;
      margin-right: 0; } }

input:first-child, select {
  margin: 0; }

.btn {
  padding: 15px;
  font-size: 19.2px;
  color: #f0f0f0;
  background-color: #6441A4;
  border: 1px solid #4e337f;
  width: 100%;
  display: block;
  text-align: center;
  margin-top: 15px;
  margin-bottom: 15px;
  text-decoration: none; }

#result {
  background-color: white;
  padding: 15px 30px;
  width: 100%;
  display: table;
  border-radius: 2px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.15);
  text-align: center;
  font-size: 48px;
  margin-bottom: 30px;
  transition: all 0.8s cubic-bezier(0.55, 0, 0.1, 1); }

#result span {
  font-size: 16px;
  color: #333;
  display: block;
  margin-bottom: 30px; }

#result h2 {
  display: table-row;
  font-weight: 100;
  color: rgba(51, 51, 51, 0.8); }

.hidden {
  visibility: none;
  opacity: 0;
  transform: translateY(-10%); }

.credits {
  font-size: 12.8px;
  text-align: center; }
